.icons-tech{
    display: inline-flex;
    border-radius:5px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center
}

.icons-tech img{
    width: 60px;
    filter: drop-shadow(0px 0px 1px #4b4b50);
    animation: flutuar 3s linear infinite;
    margin: .5rem;
    margin-top:13px ;
}

@keyframes flutuar{
    0%,
    100%{
        transform: translateY(0px)
    }
    50%{
        transform: translateY(-23px)
    }
}

.icons-tech img:nth-child(2){
    animation-delay: .2s;
}

.icons-tech img:nth-child(3){
    animation-delay: .4s;
}
.icons-tech img:nth-child(4){
    animation-delay: .6s;
}
.icons-tech img:nth-child(5){
    animation-delay: .8s;
}

.tech-profile{
    text-align: center;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    font-weight: bold;
    font-size:x-large;
    color: white;
    background-color: #151515;
    flex-direction: column;
    display: flex;
}

@media (min-width: 999px){
    .icons-tech{
        height: 100px;
        margin-top: 0 auto;
    }
}