.title-about{
color: white;
width: 100% auto;
height: auto;
}

.title-about h2, h3{
    font-family: OpenSans;
    font-weight: Bold Italic;
    text-align: center;
}
.texto-medio{
    display: flex;
    justify-content: space-between;

}

.title-about p, li{
    font-family: OpenSans;
    font-weight: Italic ;
    
}



@font-face {
    font-family: OpenSans;
    src: local("Open Sans"),
    local("Open Sans");
    font-weight: Bold, Bold Italic, Extrabold, Extrabold Italic, Italic
}

@media (min-width: 999px){
    .title-about{
        height: 200px;
    }
    
}

@media (max-width: 760px){

    .texto-medio{
    display: flex;
    flex-direction: column;
    text-align: center;

}
}