.hdr-ctn{
    background-color: #202020;
    color: white;
    box-shadow: 0px 10px 10px -6px #100F0F;
    border-radius: 0.5rem;
}

.avatar{
    height: 200px;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    position: relative;
    background: #000;
    border-radius: 5px;
    overflow: hidden;
}

.avatar-pht{ 
    width:100%; 
    height: auto; 
    max-width:160px; 
    max-height: 170px; 
    background-color: transparent;
    position: relative;
    z-index: 1;
}

.avatar::after{
    content: "";
    position: absolute;
    background: #000;
    inset: 4px;
}

.avatar::before{
    content: "";
    position: absolute;
    width: 100px;
    height: 130%;
    background: linear-gradient(#00ccff, #d400d4);
    animation: animate 4s linear infinite;
}

@keyframes animate{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.sldo-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.tlo-ctn{
    display: flex;
    justify-content: center;
    align-items: center;
}

.sldo {
display: block;
font-family: monospace;
font-size: 20px;
white-space: nowrap;
width: 15ch;
animation: typing 1s steps(15), blink .5s infinite step-end alternate;
overflow: hidden;
padding-top: 10px;
}

.sldo2{
margin: 1rem;
display: block;
font-family: monospace;
white-space: nowrap;
border-right: 4px solid;
font-size: 20px;
width: 19ch;
animation: typing 2.5s steps(19), blink .5s infinite step-end alternate;
overflow: hidden;
}

@keyframes typing {
from { width: 0}
}

@keyframes blink {
    50% {border-color: transparent}
}

@media (min-width: 999px){
.hdr-ctn{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: flex-start;
    border-radius: 0.5rem;
}
.tech-navbar{
    width: 100%;
    height: auto;
    display: none;
}
.field-avatar{
    width: 400px;
    height: auto;
}
}