.all__cards{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: 2rem;
}

.card{
    position: relative;
    width: 290px;
    height: 290px;
    color: #fff;
    border-radius:20px;
    overflow: hidden;
    margin-bottom: 20px;
}

.card-content{
    position: absolute;
    left: 0;
    bottom: 0;
    width:90%;
    padding:20px;
    background-color: #151515;
    border-top-left-radius: 20px;
    transform: translateY(70px);
    transition: .25s;
}

.card-content::before{
    content: "";
    position: absolute;
    top:-47px;
    right:-45px;
    width:100px;
    height: 100px;
    transform: rotate(-175deg);
    border-radius: 50%;
    box-shadow: inset 48px 48px #fff;
}

.card__title{
    color:#6a515e;
    line-height: 15px;
}

.card__subtitle{
    display: block;
    font-size: 12px;
    margin-bottom: 10px;
}

.card:hover .card-content{
    transform: translateY(0);
}

.card:hover .card__description{
    opacity: 1;
    transition-delay: .25s;
    text-align: center;

}

a{
    color: #6a515e;
}

.card img{
    object-fit: cover;
    height: 300px;
    width: 300px;
    cursor: pointer;
    padding-left: 7px;
}

@media (min-width: 480px){

    .card{
        width: 400px;
        height: 400px;
    }

    .card img{
        height: 400px;
        width: 400px;

    }
}

























